<script>
import { Icon } from "@iconify/vue2";

export default {
  name: 'UserPermissions',
  components: {
    Icon
  },
  data() {
    return {
      showCreateRoleModal: false,
      selectedUser: '',
      selectedRole: '',
      newRole: {
        name: '',
        description: '',
        permissions: []
      },
      // Mock data
      roles: [
        {
          id: 'reporter',
          name: 'Reporter',
          description: 'User yang dapat membuat dan mengelola laporan mereka sendiri',
          userCount: 15,
          permissions: ['create_report', 'view_own_reports', 'edit_own_reports', 'view_profile', 'edit_profile']
        },
        {
          id: 'admin',
          name: 'Admin',
          description: 'Administrator yang dapat mengelola user dan laporan',
          userCount: 3,
          permissions: ['view_all_reports', 'edit_all_reports', 'manage_users', 'view_analytics', 'manage_permissions', 'view_profile', 'edit_profile']
        }
      ],
      users: [
        { id: 1, name: 'Ahmad Pratama', email: 'ahmad@example.com', role: 'reporter' },
        { id: 2, name: 'Siti Nurhaliza', email: 'siti@example.com', role: 'admin' },
        { id: 3, name: 'Budi Santoso', email: 'budi@example.com', role: 'reporter' }
      ],
      allPermissions: [
        'create_report',
        'view_own_reports',
        'edit_own_reports',
        'view_all_reports',
        'edit_all_reports',
        'delete_reports',
        'manage_users',
        'manage_permissions',
        'view_analytics',
        'view_profile',
        'edit_profile',
        'system_settings'
      ]
    }
  },
  methods: {
    getPermissionLabel(permission) {
      const labels = {
        'create_report': 'Buat Laporan',
        'view_own_reports': 'Lihat Laporan Sendiri',
        'edit_own_reports': 'Edit Laporan Sendiri',
        'view_all_reports': 'Lihat Semua Laporan',
        'edit_all_reports': 'Edit Semua Laporan',
        'delete_reports': 'Hapus Laporan',
        'manage_users': 'Kelola User',
        'manage_permissions': 'Kelola Izin',
        'view_analytics': 'Lihat Statistik',
        'view_profile': 'Lihat Profil',
        'edit_profile': 'Edit Profil',
        'system_settings': 'Pengaturan Sistem'
      };
      return labels[permission] || permission;
    },
    
    getCurrentUserRole(userId) {
      const user = this.users.find(u => u.id === parseInt(userId));
      const role = this.roles.find(r => r.id === user?.role);
      return role?.name || 'Tidak ada role';
    },
    
    editRole(role) {
      this.$message.info(`Edit role: ${role.name}`);
      // Implement edit role functionality
    },
    
    assignRole() {
      if (!this.selectedUser || !this.selectedRole) return;
      
      const user = this.users.find(u => u.id === parseInt(this.selectedUser));
      const role = this.roles.find(r => r.id === this.selectedRole);
      
      if (user && role) {
        // Update user count in old role
        const oldRole = this.roles.find(r => r.id === user.role);
        if (oldRole) oldRole.userCount--;
        
        // Update user role
        user.role = this.selectedRole;
        
        // Update user count in new role
        role.userCount++;
        
        this.$message.success(`Role ${role.name} berhasil di-assign ke ${user.name}`);
        this.selectedUser = '';
        this.selectedRole = '';
      }
    },
    
    createRole() {
      const role = {
        id: this.newRole.name.toLowerCase().replace(/\s+/g, '_'),
        ...this.newRole,
        userCount: 0
      };
      
      this.roles.push(role);
      this.showCreateRoleModal = false;
      this.newRole = { name: '', description: '', permissions: [] };
      this.$message.success('Role baru berhasil dibuat');
    }
  }
}
</script>

